:root {

  --font-family: "Graphik LCG", sans-serif;
  --content-width: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --accent-color: #56906C;
  --dark-color: #373737;
  --grey-color: #c7c7c7;
  --light-color: #fff;
  --error-color: #b64949;
}
