.search {
  position: relative;
  flex-grow: 1;
  height: 50px;

  &__input {
    padding: 10px 40px 10px 20px;
    width: 100%;
    height: 100%;
    font-weight: 400;
    font-size: 20px;
    line-height: 1;
    border: 1px solid #56906C;
    border-radius: 10px;
    outline: none;
  }

  &__btn {
    position: absolute;
    right: 5px;
    top: 50%;
    width: 35px;
    height: 35px;
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    i {
      font-size: 20px;
      color: grey;
      transition: color 0.3s ease-in-out;
    }

    &:hover i {
      color: #56906C;
    }
  }

  &__btn-close {
    display: none;
    position: absolute;
    right: 30px;
    top: 50%;
    width: 35px;
    height: 35px;
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    i {
      font-size: 20px;
      color: grey;
      transition: color 0.3s ease-in-out;
    }

    &:hover i {
      color: #56906C;
    }
  }
}

@media (max-width: 768px) {
  .search {
    width: 100%;
  }
}

@media (max-width: 420px) {
  .search {
    width: 100%;
  }
}
